/* general.css */

/* Definir variables globales */
:root {
    /* ------------- COLORES ------------- */
    --primary-color-wine: #682621;
    --secondary-color-green: #40723E;
    --third-color-red: #B01A2F;
    --fourth-color-black: #2C2C29;
    --white-color: #fff;      
    /* -------------- LETRAS -------------- */    
    --font-family-imagen: 'Futura MD BT', sans-serif;    
    --font-family-bold: 'Arial Unicode MS', sans-serif;
    --font-size-base: 16px;       
  }

  
  /* Estilos generales */
  body {
    font-family: var(--font-family-bold);
    /* font-size: var(--font-size-base);    */
    color: var(--primary-color-wine);
    background-color: #f0f0f0;    
    /* font-weight: bold; */
    /* font-style: italic; */
  }

  .icons{
    font-size: 60px;
    padding: 10px;
  }

