/* Reset CSS */
/* ================================ */
  /* Listas sin viñetas */
  body, html {
    margin: 0;
    padding: 0;
  }

  ul, ol {
    list-style: none;
  }
  
  /* Estilos para enlaces */
  a {
    text-decoration: none;
    color: inherit;
  }
  
  a:hover {
    transform: scale(1.1); /* Aumentar el tamaño en 10% */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* Agregar sombra */
  }

  /* Eliminar el estilo de botón predeterminado */
  button {
    background: none;
    cursor: pointer;
  }
  
  /* Estilos para inputs y textarea */
  input, textarea {
    border: none;
    outline: none;
    font-family: inherit;
    font-size: inherit;
    padding: 0.25em;
  }
  
  /* Estilo para elementos deshabilitados */
  [disabled] {
    opacity: 0.5;
    pointer-events: none;
  }
  
  /* Seleccionar elementos deshabilitados */
  ::selection {
    background-color: #dcdcdc;
  }
  