/* Navbar.css */
.navbar {
    background-color: var(--primary-color-wine);
    color: var(--white-color);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
  }
  
  .navbar-left {
    display: flex;
    align-items: center;    
  }
  
  .navbar-logo{
    height: 50px;
    margin-bottom: 10px;
    margin-right: 10px;
    filter: brightness(0) invert(1);
  }

  .navbar-divider {
    width: 2px;
    height: 60px;
    background-color: #fff;
    margin: 0 20px;
  }
  
  .navbar-icon{
    font-size: 60px;    
  }
  
  .conocenos-link{
    font-weight: bold;
    font-size: 20px;
}
  
  .nav-links {
    list-style: none;
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
  }
  
  .nav-links li {
    margin-left: 20px;
    
  }
  
  .navbar-right {
    display: flex;
    align-items: center;
  }
  
   