 .widget-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    margin-top: 20px;
    background-color: #d9e4f4;
    z-index: -1;    
  }
  
  
  .widget-text {
    flex: 1;    
    color: var(--fourth-color-black);
    width: 500px;
    font-size: 25px;
  }

    
  .widget-text-b {
    flex: 1;    
    color: var(--white-color);
    width: 300px;
    font-size: 25px;
    margin-left: 150px;
  }
  
  .title-conocenos {
    font-size: 35px;
    font-weight: bold;
  }
  
  .widget-image {
    flex: 1;
    display: flex;
    justify-content: flex-end;
    padding: 20px;
  }
  
  .widget-image img {
    width: 850px;
    height:auto;
  }
  
  .carousel-container {
    margin-top: 40px;
    background-color:  #d9e4f4;
    /* width: 100%; */
    height: 400px;
    margin-bottom: 40px;
  }
  
  .carousel-slot {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    /* color: white; */
    height: 400px;
    background-color: #d9e4f4;
    padding-left: 300px; /* Ajusta este valor según lo que necesites */
    padding-right: 300px; /* Ajusta este valor según lo que necesites */
    
  }

  /* Estilos para las cards debajo del carrusel */
  .card-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 40px;
    gap: 40px;
    
  }
  
  .card {
    position: relative;
    height: 300px;
    width: 500px;
    border-radius: 20px;
    border: 2px solid black;
    overflow: hidden;
  }
  
  .card::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    height: 100%;
    background-color: #d9e4f4;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;    
    z-index: -1;
   
  }
  
  .card::after {
    content: "";
    position: absolute;
    top: 0;
    left: 35%;
    width: 100%;
    height: 100%;
    background-color: white;    
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;        
    border-top-left-radius: 90px;
    border-bottom-left-radius: 90px;
    z-index: -1;
    
  }

  .card:hover{
    cursor: pointer;
    transform: scale(1.05);
  }
  

  .text-container{
    display: flex;
    flex-direction: column;    
    align-items: flex-start;
    margin-left: 63px;

  }
.card-title {
    font-size: 35px;
    font-weight: bold;
    color: black;
    text-align: center; /* Centra el título */
    margin-bottom: 10px;
  }
  .card-content {
    display: flex;   
    align-items: center;    
  }
  
 
  
  .card-image {
    height: 150px;    
  }


  
  .card-text {    
    margin-top: 0;
    font-size: 25px;
    color: var(--fourth-color-black);
    
  }
    
  
  .card-center-image {
    display: flex;
    justify-content: center;
    align-items: center;
    
  }

  .card-image-container{
    margin-left: 20px;
  }
  
  .widget-content-bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    margin-top: 20px;
    color: var(--white-color);
    background-color: var(--primary-color-wine);
       
  }

  .title-bottom {
    font-size: 35px;
    font-weight: bold;
    color: var(--white-color);
    
  }

  .widget-image2 {
    flex: 1;
    display: flex;
    justify-content: center;
    padding: 20px;
  }

  
  


  
  